.nav-bar{
    position: relative;
    left:0;
    top:0;
    max-width: 280px;
    background: #2E2E2E;   
    height: 100%;
    font-family: 'Noto Sans', sans-serif;
    
}


.nav-bar ul{
    list-style: none;
    color:white;
    font-size: 20px;
    font-weight: 500;
    margin-bottom: 50px;
    transition: all 0.6s ease;
    text-decoration: none;
}
a{
    color: white;
    text-decoration: none;
}
.nav-bar li{
    padding: 20px 20px  20px 50px ;
    margin-left: 0;
    color:white;
    text-decoration: none;
}
.nav-bar .nav-items{
    margin-top: 100px;
}
.nav-bar li:hover{
   background: url( data:image/jpeg;base64,
    ),no-repeat;
    background-size: cover;
}
@media (max-width:1200px) {
    .nav-bar{
        width: 100%;
        max-width: none;
        display: flex;
        justify-content: center;
        position: absolute;
        z-index: 10;
        height: 100px;
        display: flex;
        align-items: center;
        margin: 0;
        padding: 0;
    }
    .nav-bar ul{
        margin-top: 0;
        height: 100%;
        display: flex;  
        align-items: center;
        width: 100%;
        color: white;
        z-index: 10;
        margin-bottom: 0;
    }
    .nav-bar .nav-items{
        margin-top: 0px;
        display: flex;
        align-items: center;
    }
    .nav-bar li{
        display: flex;
       align-items: center;
        padding: 10px;
        font-size: 16px;
        margin-bottom: 0;
        line-height:normal;
    }
    .nav-bar li:hover{
        background:url();
    }
    .nav-bar #lk{
        display: none;
    }
}
@media (max-width:700px) {
    .nav-bar{
        width: 100%;
        
        display: flex;
        justify-content: center;
        height: 70px;
    }
    .nav-bar li:nth-child(3){
        display: none;
    }
    .nav-bar li:nth-child(4){
        display: none;
    }
}